/* About Us Section */
.about-page {
    padding: 40px;
    max-width: 800px;
    margin: auto;
    text-align: center;
    color: var(--text-color);
    border-radius: 10px;
    box-shadow: none;
}

.about-page h2 {
    font-size: 32px;
    color: var(--accent-color);
    margin-bottom: 20px;
}

.about-page p {
    font-size: 18px;
    line-height: 1.6;
    color: var(--text-color);
    margin-bottom: 20px;
}

/* Contact Us Section */
.contact-us {
    padding: 32px;
    max-width: 800px;
    margin: 40px auto;
    text-align: left;
    border-radius: 10px;
    box-shadow: none;
}

.contact-us h2 {
    text-align: center;
    color: var(--accent-color);
    margin-bottom: 20px;
    font-size: 28px;
}

.contact-us p {
    color: var(--text-color);
    margin-bottom: 20px;
    font-size: 16px;
}

/* Contact Form Styling */
.contact-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 16px;
    background-color: var(--background-color);
    border-radius: 10px;
}

.contact-form label {
    color: var(--text-color);
    font-weight: bold;
}

.contact-form input,
.contact-form textarea {
    padding: 12px;
    border: 1px solid var(--text-color);
    border-radius: 5px;
    background-color: var(--background-color);
    color: var(--text-color);
    font-size: 16px;
}

.contact-form input:focus,
.contact-form textarea:focus {
    outline: none;
    border-color: var(--accent-color);
}

.contact-form button {
    padding: 12px;
    background-color: var(--accent-color);
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-form button:hover {
    background-color: var(--text-heading);
}

.form-status {
    margin-top: 20px;
    color: var(--accent-color);
    font-weight: bold;
    text-align: center;
    font-size: 18px;
}

/* Embedded Video Section */
.video-wrapper {
    text-align: center;
    margin: 40px auto;
    max-width: 560px;
    padding: 16px;
    border-radius: 10px;
    box-shadow: none;
}

.video-wrapper h3 {
    font-size: 24px;
    color: var(--accent-color);
    margin-bottom: 16px;
    text-align: center;
}

.video-wrapper iframe {
    width: 100%;
    max-width: 100%;
    height: 315px;
    border-radius: 10px;
    border: none;
}

/* Media Query */
@media (max-width: 768px) {
    .about-page,
    .contact-us {
        padding: 20px;
    }

    .video-wrapper {
        margin: 20px auto;
    }

    .contact-form {
        gap: 12px;
    }

    .contact-form button {
        font-size: 14px;
        padding: 10px;
    }
}